import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Paper from "@material-ui/core/Paper";
import Grid from "@material-ui/core/Grid";
import Card from "@material-ui/core/Card";
import CardActionArea from "@material-ui/core/CardActionArea";
import CardActions from "@material-ui/core/CardActions";
import CardContent from "@material-ui/core/CardContent";
import CardMedia from "@material-ui/core/CardMedia";
import Button from "@material-ui/core/Button";
import Typography from "@material-ui/core/Typography";
import Box from "@material-ui/core/Box";
import Rating from "@material-ui/lab/Rating";
import StarBorderIcon from "@material-ui/icons/StarBorder";

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    width: "90%",
    margin: "auto",
    marginTop: 50,
  },
  paper: {
    padding: theme.spacing(2),
    textAlign: "center",
    color: theme.palette.text.secondary,
  },
}));

const returnCard = (imgSrc, title, description, rating) => (
  <Card>
    <CardActionArea>
      <CardMedia
        component="img"
        alt={title}
        height="140"
        image={imgSrc}
        title={title}
      />
      <CardContent>
        <Typography gutterBottom variant="h5" component="h2">
          {title}
        </Typography>
        <Typography variant="body2" color="textSecondary" component="p">
          {description}
        </Typography>
        <Typography
          variant="h5"
          color="textSecondary"
          component="p"
          style={{ marginTop: 10 }}
        >
          $118.25
        </Typography>
      </CardContent>
      <Box component="fieldset" mb={3} borderColor="transparent">
        <Rating name="read-only" value={rating} readOnly />
      </Box>
    </CardActionArea>
    <CardActions>
      <Button size="small" style={{ color: "black" }}>
        <b>More Details</b>
      </Button>
      <Button size="small" style={{ color: "#00a499" }}>
        <b>Add To Cart</b>
      </Button>
    </CardActions>
  </Card>
);

const Results = (props) => {
  const classes = useStyles();

  return (
    <div className={classes.root}>
      <Grid container spacing={3}>
        <Grid item xs={3}>
          {returnCard(
            "https://s3.img-b.com/image/private/c_lpad,f_auto,h_1200,t_base,w_1200/v3/product/kohler/kohler-k-193-4-2bz-390.jpg",
            "Kohler",
            "Devonshire Single Hole Bathroom Faucet - Drain Assembly Included",
            3
          )}
        </Grid>
        <Grid item xs={3}>
          {returnCard(
            "https://s3.img-b.com/image/private/c_lpad,f_auto,h_1200,t_base,w_1200/v3/product/delta/delta-567lf-pp-486.jpg",
            "Delta",
            "Angular Modern Single Hole Bathroom Faucet with Pop-Up Drain Assembly - Includes Lifetime Warranty",
            4
          )}
        </Grid>
        <Grid item xs={3}>
          {returnCard(
            "https://s3.img-b.com/image/private/c_lpad,f_auto,h_1200,t_base,w_1200/v3/product/miseno/miseno-bella-mno301bnp-front-main.jpg",
            "Miseno",
            "Bella Single Hole Bathroom Faucet - Includes Pop-Up Drain Assembly and Optional Deck Plate",
            3
          )}
        </Grid>
        <Grid item xs={3}>
          {returnCard(
            "https://s3.img-b.com/image/private/c_lpad,f_auto,h_1200,t_base,w_1200/v3/product/signaturehardware/signature-hardware-404257-1489527.jpg",
            "Signature Hardware",
            "Pagosa Single Hole Waterfall Vessel Bathroom Faucet with Pop-Up Drain Assembly",
            5
          )}
        </Grid>
        <Grid item xs={3}>
          {returnCard(
            "https://s3.img-b.com/image/private/c_lpad,f_auto,h_1200,t_base,w_1200/v3/product/grohe/grohe-3427000a.jpg",
            "Grohe",
            "Concetto 1.2 GPM Single Handle Single Hole Bathroom Faucet with SilkMove Ceramic Disc Cartridge - Free Metal Drain Assembly with purchase",
            2
          )}
        </Grid>
      </Grid>
    </div>
  );
};

export default React.memo(Results);
