import { React, useState } from "react";
// Core Components ////////
import { makeStyles, fade } from "@material-ui/core/styles";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import Typography from "@material-ui/core/Typography";
import List from "@material-ui/core/List";
import Divider from "@material-ui/core/Divider";
import ListItem from "@material-ui/core/ListItem";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import ListItemText from "@material-ui/core/ListItemText";
import IconButton from "@material-ui/core/IconButton";
import SwipeableDrawer from "@material-ui/core/SwipeableDrawer";
import Slide from "@material-ui/core/Slide";
import useScrollTrigger from "@material-ui/core/useScrollTrigger";
import InputBase from "@material-ui/core/InputBase";
import InputLabel from "@material-ui/core/InputLabel";
import MenuItem from "@material-ui/core/MenuItem";
import ListSubheader from "@material-ui/core/ListSubheader";
import FormControl from "@material-ui/core/FormControl";
import Select from "@material-ui/core/Select";
// Badge///////
import Badge from "@material-ui/core/Badge";
// Styles /////////////////
import { withStyles } from "@material-ui/core/styles";
/// Icons ////////////////
import MenuIcon from "@material-ui/icons/Menu";
import SearchIcon from "@material-ui/icons/Search";
import AccountBoxIcon from "@material-ui/icons/AccountBox";
import ChatBubbleIcon from "@material-ui/icons/ChatBubble";
import ShoppingCartIcon from "@material-ui/icons/ShoppingCart";
import StarsIcon from "@material-ui/icons/Stars";
import HelpOutlineIcon from "@material-ui/icons/HelpOutline";
import PhoneIcon from "@material-ui/icons/Phone";
import SchoolIcon from "@material-ui/icons/School";
// Imported images /////////
import logo1 from "../images/logo1.png";

const primaryIcons = [
  <AccountBoxIcon />,
  <ShoppingCartIcon />,
  <StarsIcon style={{ color: "orange" }} />,
  <SchoolIcon />,
];

const secondaryIcons = [<ChatBubbleIcon />, <PhoneIcon />, <HelpOutlineIcon />];

//Styling///
const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  nav: {
    backgroundColor: "white",
    display: "flex",
    flexWrap: "nowrap",
  },
  logo1: {
    width: 120,
    height: 60,
    marginTop: 5,
  },
  menuButton: {
    transition: "all 0.2s ease-in-out",
    color: "#00a499",
    marginRight: theme.spacing(1),
    "&:hover": {
      backgroundColor: fade(theme.palette.common.black, 0.25),
    },
  },
  title: {
    marginLeft: 20,
    flexGrow: 1,
    display: "none",
    [theme.breakpoints.up("sm")]: {
      display: "block",
    },
  },
  search: {
    transition: "all 0.2s ease-in-out",
    position: "relative",
    borderRadius: theme.shape.borderRadius,
    backgroundColor: fade(theme.palette.common.black, 0.15),
    "&:hover": {
      backgroundColor: fade(theme.palette.common.black, 0.25),
    },
    
    width: "100%",
    [theme.breakpoints.up("xs")]: {
      marginLeft: theme.spacing(1),
      marginRight: theme.spacing(2),
      width: "auto",
    },
  },
  searchIcon: {
    color: "#00a499",
    padding: theme.spacing(0, 2),
    height: "100%",
    position: "absolute",
    pointerEvents: "none",
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
  },
  inputRoot: {
    color: "black",
  },
  inputInput: {
    padding: theme.spacing(1, 1, 1, 0),
    // vertical padding + font size from searchIcon
    paddingLeft: `calc(1em + ${theme.spacing(4)}px)`,
    transition: theme.transitions.create("width"),
    width: "100%",

    [theme.breakpoints.up("sm")]: {
      width: "25ch",
      "&:focus": {
        width: "40ch",
      },
    },
    
  },
  list: {
    
    width: 250,
  },
  listItem: {
    "&:hover": {
      backgroundColor: fade(theme.palette.common.black, 0.25),
    },
  },
  listItemIcon: {
    color: "#00a499",
  },
  select: {
    flexGrow: 1,
    marginLeft: 10,
    marginBottom: 12,
  },
  formControl: {
    minWidth: "32vh",
    [theme.breakpoints.up("sm")]: {
      width: "35vh",
    },
  },
  formSelect: {
    "&:before": {
      borderColor: "#00a499",
    },
    "&:after": {
      borderColor: "#00a499",
    },
  },
  selectLabel: {},
}));
//End of Styling///

//Badge Styling///
const StyledBadge = withStyles((theme) => ({
  badge: {
    right: -3,
    top: 13,
    border: `2px solid ${theme.palette.background.paper}`,
    padding: "0 4px",
  },
}))(Badge);
//End of Badge Styling///

const Navbar = (props) => {
  const classes = useStyles();

  //Hooks for Drawer
  const [state, setState] = useState({
    top: false,
    left: false,
    bottom: false,
    right: false,
  });
  //End of Hooks for Drawer

  //Hide navbar on scroll///
  function HideOnScroll(props) {
    const { children, window } = props;
    const trigger = useScrollTrigger({ target: window ? window() : undefined });
    return (
      <Slide appear={false} direction="down" in={!trigger}>
        {children}
      </Slide>
    );
  }
  //End of Hide navbar on scroll///

  //BuildWithFerguson logo variable
  const logoImage1 = (
    <img src={logo1} alt="companyLogo" className={classes.logo1} />
  );

  // Select /////////
  const groupedSelect = () => (
    <div>
      <FormControl className={classes.formControl}>
        <InputLabel htmlFor="grouped-select" className={classes.selectLabel}>
          What are you looking for?
        </InputLabel>
        <Select
          defaultValue=""
          id="grouped-select"
          className={classes.formSelect}
        >
          <MenuItem value="">
            <em>Nothing</em>
          </MenuItem>
          <ListSubheader>Bathroom</ListSubheader>
          <MenuItem value={1}>Showers</MenuItem>
          <MenuItem value={2}>Bathtubs</MenuItem>
          <ListSubheader>Kitchen</ListSubheader>
          <MenuItem value={3}>Accessories</MenuItem>
          <MenuItem value={4}>Faucets</MenuItem>
        </Select>
      </FormControl>
    </div>
  );
  // End of Select /////////

  //Drawer//
  const toggleDrawer = (anchor, open) => (event) => {
    if (
      event &&
      event.type === "keydown" &&
      (event.key === "Tab" || event.key === "Shift")
    ) {
      return;
    }
    setState({ ...state, [anchor]: open });
  };

  let shoppingCart = (index) => (
    <StyledBadge badgeContent={1} color="secondary">
      {primaryIcons[index]}
    </StyledBadge>
  );
  let liveChat = (index) => (
    <StyledBadge badgeContent={2} color="secondary">
      {secondaryIcons[index]}
    </StyledBadge>
  );

  const list = (anchor) => (
    <div
      className={classes.list}
      onClick={toggleDrawer(anchor, false)}
      onKeyDown={toggleDrawer(anchor, false)}
    >
      <Typography className={classes.drawerLogo}>
              {logoImage1}
            </Typography>
      <List>
        {["Account", "Shopping Cart", "Become a PRO", "Learning Center"].map(
          (text, index) => (
            <ListItem button key={text} className={classes.listItem}>
              <ListItemIcon className={classes.listItemIcon}>
                {index === 1 ? shoppingCart(index) : primaryIcons[index]}
              </ListItemIcon>
              <ListItemText primary={text} />
            </ListItem>
          )
        )}
      </List>
      <Divider />
      <List>
        {["Live Chat", "(800) 375-3403", "Help"].map((text, index) => (
          <ListItem button key={text} className={classes.listItem}>
            <ListItemIcon className={classes.listItemIcon}>
            {index === 0 ? liveChat(index) : secondaryIcons[index]}
            </ListItemIcon>
            <ListItemText primary={text} />
          </ListItem>
        ))}
      </List>
    </div>
  );
  //End of Drawer//

  return (
    <div className={classes.root}>
      <HideOnScroll {...props}>
        <AppBar className={classes.nav}>
          <Toolbar>
            <IconButton
              onClick={toggleDrawer("left", true)}
              edge="start"
              className={classes.menuButton}
              color="inherit"
              aria-label="open drawer"
            >
              <StyledBadge badgeContent={3} color="secondary">
              <MenuIcon />
              </StyledBadge>
            </IconButton>
            <Typography className={classes.title}>
              {logoImage1}
            </Typography>
            <Typography className={classes.select}>
              {groupedSelect()}
            </Typography>
            <div className={classes.search}>
              <div className={classes.searchIcon}>
                <SearchIcon />
              </div>
              <InputBase
                placeholder="Search.."
                classes={{
                  root: classes.inputRoot,
                  input: classes.inputInput,
                }}
                inputProps={{ "aria-label": "search" }}
              />
            </div>
          </Toolbar>
        </AppBar>
      </HideOnScroll>
      <SwipeableDrawer
        anchor="left"
        open={state["left"]}
        onClose={toggleDrawer("left", false)}
        onOpen={toggleDrawer("left", true)}
      >
        {list("left")}
      </SwipeableDrawer>
    </div>
  );
};

export default Navbar;
