import React from "react";

const Footer = () => {
  return (
    
      <div
        style={{marginTop: 50, postion: "fixed", height: "200px", backgroundColor: "grey", width: "100%",  display: "inline-flex", justifyContent:"space-around", }}
      >
        <div style={{color: "white"}}>
            <h4>Customer Service</h4>
            <ul style={{listStyle: "none"}}>
                <li>Help Center</li>
                <li>Contact Us</li>
                <li>Returns</li>
                <li>Shhipping Policy</li>
                <li>Subscribe/Unsubscribe</li>
            </ul>
        </div>
        <div style={{color: "white"}}>
            <h4>Account</h4>
            <ul style={{listStyle: "none"}}>
                <li>My Account</li>
                <li>Order Status</li>
                <li>My Cart</li>
            </ul>
        </div>
        <div style={{color: "white"}}>
            <h4>Company Info</h4>
            <ul style={{listStyle: "none"}}>
                <li>About Us</li>
                <li>Careers</li>
                <li>Site Map</li>
            </ul>
        </div>
      </div>
    
  );
};

export default Footer;
