import React from "react";
import Carousel from "react-material-ui-carousel";
import { makeStyles} from "@material-ui/core/styles";
import imageSrc1 from "../images/carousel/1.jpg";
import imageSrc2 from "../images/carousel/2.jpg";
import imageSrc3 from "../images/carousel/3.jpg";


var image1 = (
  <img src={imageSrc1} alt="random" style={{ width: "100%", height: "100%" }} />
);
var image2 = (
  <img src={imageSrc2} alt="random" style={{ width: "100%", height: "100%" }} />
);


const useStyles = makeStyles((theme) => ({
  container: {
    marginTop: 70,
    width: "100%",
    display: "inline-flex",
    height: "600px",
    fontFamily: "Candara",
  },
  heading: {
    transition: "all 0.2s ease-in",
    width: "100%",
    backgroundColor: "#00a499",
    textAlign: "center",
    "&:hover": {
      backgroundColor: "#084d48",
    },
    [theme.breakpoints.up("sm")]: {
      width: "30%",
    },
  },
  headingContent: {
    color: "white",
    marginTop: 200,
  },
  heading1: {
    transition: "all 0.2s ease-in",
    width: "100%",
    backgroundColor: "#00a499",
    textAlign: "center",
    "&:hover": {
      backgroundColor: "#084d48",
    },
    [theme.breakpoints.up("sm")]: {
      width: "40%",
    },
  },
  headingContent1: {
    color: "white",
    marginTop: 200,
  },
  heading2: {
    background: "linear-gradient(rgba(0,0,0, .5), rgba(0,0,0, .5)), url(" + imageSrc3 + ")",
    transition: "all 0.3s ease-in-out",
    width: "100%",
    textAlign: "center",
    [theme.breakpoints.up("sm")]: {
      width: "100%",
    },
  },
  headingContent2: {
    color: "white",
    marginTop: 200,
  },
  first: {
    width: 0,
    [theme.breakpoints.up("sm")]: {
      width: "70%",
    },
  },
  second: {
    width: 0,
    [theme.breakpoints.up("sm")]: {
      width: "60%",
    },
  },
  third: {
    width: 0,
    [theme.breakpoints.up("sm")]: {
      width: "50%",
    },
  },
 
  btn: {
    transition: "all 0.2s ease-in-out",
    padding: 20,
    color: "white",
    fontSize: "25px",
    borderRadius: "25px",
    backgroundColor: "transparent",
    border: "solid thin white",
    "&:hover": {
      color: "black",
      backgroundColor: "white",
      cursor: "pointer",
    },
  },
}));



const Header = (props) => {
  const classes = useStyles();

  var items = [
    <div className="container" className={classes.container}>
      <div className={classes.heading}>
        <div className={classes.headingContent}>
          <h1>20% Off Kitchen Supplies!</h1>
          <button className={classes.btn}>Save Now</button>
        </div>
      </div>
      <div className={classes.first}>{image2}</div>
    </div>,
    <div className="container" className={classes.container}>
      <div className={classes.second}>{image1}</div>
      <div className={classes.heading1}>
        <div className={classes.headingContent1}>
          <h1>Make your bathroom prettier</h1>
          <h3>Insane 50% sale now available!</h3>
          <button className={classes.btn}>Save Now</button>
        </div>
      </div>
    </div>,
    <div className="container" className={classes.container}>
      
      <div className={classes.heading2}>
        <div className={classes.headingContent2}>
          <h1 style={{fontSize: "45px"}}>Can't see well?</h1>
          <p style={{fontSize: "25px"}}>Get the best lighting for your home for 40% off!</p>
          <button className={classes.btn}>Save Now</button>
        </div>
      </div>
    </div>,
  ];

  return (
    <div style={{ width: "100%", alignItems: "center" }}>
      <Carousel indicators="false" animation="fade" navButtonsAlwaysVisible="true">
        {items.map((item, i) => item)}
      </Carousel>
    </div>
  );
};

export default Header;
