import React from "react";
import PropTypes from "prop-types";
import { withStyles, makeStyles } from "@material-ui/core/styles";
import Slider from "@material-ui/core/Slider";
import Typography from "@material-ui/core/Typography";
import Tooltip from "@material-ui/core/Tooltip";
import FormGroup from "@material-ui/core/FormGroup";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import Checkbox from "@material-ui/core/Checkbox";
import InputLabel from "@material-ui/core/InputLabel";
import MenuItem from "@material-ui/core/MenuItem";
import FormHelperText from "@material-ui/core/FormHelperText";
import FormControl from "@material-ui/core/FormControl";
import Select from "@material-ui/core/Select";

const useStyles = makeStyles((theme) => ({
  root: {
    textAlign: "center",
    width: "90%",
    margin: "auto",
    marginTop: 30,
  },
  formControl: {
    margin: theme.spacing(1),
    minWidth: 120,
  },
  selectEmpty: {
    marginTop: theme.spacing(2),
  },

  FormControlLabel: {
    fontSize: 20,
  },
  FormControlLabel1: {
    fontSize: 20,
    marginRight: 10,
  },
}));

const PrettoSlider = withStyles({
    root: {
      color: "#00a499",
      height: 8,
      width: "25vh",
    },
    thumb: {
      height: 24,
      width: 24,
      backgroundColor: "#fff",
      border: "2px solid currentColor",
      marginTop: -8,
      marginLeft: -12,
      "&:focus, &:hover, &$active": {
        boxShadow: "inherit",
      },
    },
    active: {},
    valueLabel: {
      left: "calc(-50% + 4px)",
    },
    track: {
      height: 8,
      borderRadius: 4,
    },
    rail: {
      height: 8,
      borderRadius: 4,
    },
  })(Slider);

const Filters = (props) => {
  const [age, setAge] = React.useState("");

  const handleChange = (event) => {
    setAge(event.target.value);
  };
 

  const classes = useStyles();
  return (
    <div className={classes.root}>
        <h1 style={{textAlign: "left", fontFamily: "Bahnschrift"}}>Bathroom Faucets</h1>
      <FormGroup row>
        
        <FormControlLabel
          style={{ marginTop: 1, marginRight: 20, marginLeft: 40 }}
          classes={{
            label: classes.FormControlLabel1,
          }}
          control={
            <PrettoSlider
              valueLabelDisplay="auto"
              aria-label="Max Price"
              defaultValue={45}
            />
          }
          labelPlacement="start"
          label="Maximum Price"
        />
        <FormControlLabel
          classes={{
            label: classes.FormControlLabel,
          }}
          control={<Checkbox name="freeShipping" style={{color: "orangered"}} />}
          labelPlacement="start"
          label="Free Shipping"
        />
        <FormControlLabel
          classes={{
            label: classes.FormControlLabel,
          }}
          control={<Checkbox name="onSale" style={{color: "orangered"}} />}
          labelPlacement="start"
          label="On Sale"
        />
        <FormControlLabel
          classes={{
            label: classes.FormControlLabel,
          }}
          control={<Checkbox name="newProduct" style={{color: "orangered"}} />}
          labelPlacement="start"
          label="New"
        />
      </FormGroup>
      
      <FormControl className={classes.formControl}>
        <InputLabel id="demo-simple-select-helper-label">Brand</InputLabel>
        <Select
          labelId="demo-simple-select-helper-label"
          id="demo-simple-select-helper"
          value={age}
          onChange={handleChange}
        >
          <MenuItem value="">
            <em>None</em>
          </MenuItem>
          <MenuItem value={10}>Kohler (82)</MenuItem>
          <MenuItem value={20}>Delta (211)</MenuItem>
          <MenuItem value={30}>Moen (80)</MenuItem>
          <MenuItem value={30}>Hansgrohe (78)</MenuItem>
          <MenuItem value={30}>Miseno (18)</MenuItem>
        </Select>
        {/* <FormHelperText>Some important helper text</FormHelperText> */}
      </FormControl>
      <FormControl className={classes.formControl}>
        <InputLabel id="demo-simple-select-helper-label">Colors</InputLabel>
        <Select
          labelId="demo-simple-select-helper-label"
          id="demo-simple-select-helper"
          value={age}
          onChange={handleChange}
        >
          <MenuItem value="">
            <em>None</em>
          </MenuItem>
          <MenuItem value={10}>Chromes (1843)</MenuItem>
          <MenuItem value={20}>Nickel Tones (915)</MenuItem>
          <MenuItem value={30}>Bronze Tones (349)</MenuItem>
          <MenuItem value={30}>Blacks (258)</MenuItem>
          <MenuItem value={30}>Stainless Steel (203)</MenuItem>
          <MenuItem value={30}>Gold Tones (160)</MenuItem>
          <MenuItem value={30}>Brass Tones (98)</MenuItem>
        </Select>
        {/* <FormHelperText>Some important helper text</FormHelperText> */}
      </FormControl>
      <FormControl className={classes.formControl}>
        <InputLabel id="demo-simple-select-helper-label">Theme</InputLabel>
        <Select
          labelId="demo-simple-select-helper-label"
          id="demo-simple-select-helper"
          value={age}
          onChange={handleChange}
        >
          <MenuItem value="">
            <em>None</em>
          </MenuItem>
          <MenuItem value={10}>Modern (716)</MenuItem>
          <MenuItem value={20}>Contemporary (299)</MenuItem>
          <MenuItem value={30}>Commercial (280)</MenuItem>
          <MenuItem value={30}>Basic (203)</MenuItem>
          <MenuItem value={30}>Traditional (145)</MenuItem>
          <MenuItem value={30}>Transitional (122)</MenuItem>
          <MenuItem value={30}>Vintage (40)</MenuItem>
        </Select>
        {/* <FormHelperText>Some important helper text</FormHelperText> */}
      </FormControl>
      <FormControl className={classes.formControl}>
        <InputLabel id="demo-simple-select-helper-label">Handle Style</InputLabel>
        <Select
          labelId="demo-simple-select-helper-label"
          id="demo-simple-select-helper"
          value={age}
          onChange={handleChange}
        >
          <MenuItem value="">
            <em>None</em>
          </MenuItem>
          <MenuItem value={10}>Lever (1109)</MenuItem>
          <MenuItem value={20}>Electronic (334)</MenuItem>
          <MenuItem value={30}>Cross (47)</MenuItem>
          <MenuItem value={30}>Knob (42)</MenuItem>
          <MenuItem value={30}>Push Button (28)</MenuItem>
          <MenuItem value={30}>Joystick (9)</MenuItem>
          <MenuItem value={30}>Wrist Blade (3)</MenuItem>
        </Select>
        {/* <FormHelperText>Some important helper text</FormHelperText> */}
      </FormControl>
      <FormControl className={classes.formControl}>
        <InputLabel id="demo-simple-select-helper-label">Spout Type</InputLabel>
        <Select
          labelId="demo-simple-select-helper-label"
          id="demo-simple-select-helper"
          value={age}
          onChange={handleChange}
        >
          <MenuItem value="">
            <em>None</em>
          </MenuItem>
          <MenuItem value={10}>Fixed (1371)</MenuItem>
          <MenuItem value={20}>Swivel (59)</MenuItem>
        </Select>
        {/* <FormHelperText>Some important helper text</FormHelperText> */}
      </FormControl>
      <FormControl className={classes.formControl}>
        <InputLabel id="demo-simple-select-helper-label">Features</InputLabel>
        <Select
          labelId="demo-simple-select-helper-label"
          id="demo-simple-select-helper"
          value={age}
          onChange={handleChange}
        >
          <MenuItem value="">
            <em>None</em>
          </MenuItem>
          <MenuItem value={10}>Waterfall Faucet (69)</MenuItem>
          <MenuItem value={20}>Includes Drain Assembly (686)</MenuItem>
          <MenuItem value={20}>Includes Escutcheon (353)</MenuItem>
          <MenuItem value={20}>Handles Included (1425)</MenuItem>
          <MenuItem value={20}>Touch/Touchless (472)</MenuItem>
        </Select>
        {/* <FormHelperText>Some important helper text</FormHelperText> */}
      </FormControl>
      <FormControl className={classes.formControl}>
        <InputLabel id="demo-simple-select-helper-label">Certifications</InputLabel>
        <Select
          labelId="demo-simple-select-helper-label"
          id="demo-simple-select-helper"
          value={age}
          onChange={handleChange}
        >
          <MenuItem value="">
            <em>None</em>
          </MenuItem>
          <MenuItem value={10}>ADA Compilant (1513)</MenuItem>
          <MenuItem value={20}>CSA Approved (807)</MenuItem>
          <MenuItem value={20}>UPC Approved (340)</MenuItem>
          <MenuItem value={20}>Low Lead Compliant (1269)</MenuItem>
          
        </Select>
        {/* <FormHelperText>Some important helper text</FormHelperText> */}
      </FormControl>
      <FormControl className={classes.formControl}>
        <InputLabel id="demo-simple-select-helper-label">Flow Rate</InputLabel>
        <Select
          labelId="demo-simple-select-helper-label"
          id="demo-simple-select-helper"
          value={age}
          onChange={handleChange}
        >
          <MenuItem value="">
            <em>None</em>
          </MenuItem>
          <MenuItem value={10}>Ca Drought Compliant (1347)</MenuItem>
          <MenuItem value={20}>Up to 1 GPM (536)</MenuItem>
          <MenuItem value={20}>1 to 2 GPM (1279)</MenuItem>
          <MenuItem value={20}>2 to 3 GPM (86)</MenuItem>
          
        </Select>
        {/* <FormHelperText>Some important helper text</FormHelperText> */}
      </FormControl>
      <FormControl className={classes.formControl}>
        <InputLabel id="demo-simple-select-helper-label">Valve Type</InputLabel>
        <Select
          labelId="demo-simple-select-helper-label"
          id="demo-simple-select-helper"
          value={age}
          onChange={handleChange}
        >
          <MenuItem value="">
            <em>None</em>
          </MenuItem>
          <MenuItem value={10}>Ceramic Disc (970)</MenuItem>
          <MenuItem value={20}>Solenoid (510)</MenuItem>
          <MenuItem value={20}>Washerless Catridge (38)</MenuItem>
          <MenuItem value={20}>Thermostatic (34)</MenuItem>
          <MenuItem value={20}>Self-Closing (9)</MenuItem>
          <MenuItem value={20}>Metering (8)</MenuItem>
          <MenuItem value={20}>Washerless (5)</MenuItem>
          
        </Select>
        {/* <FormHelperText>Some important helper text</FormHelperText> */}
      </FormControl>
      
    </div>
  );
};

export default React.memo(Filters);
